import { h } from '../../../node_modules/preact/dist/preact.mjs'
import { Component } from '../../../node_modules/preact/dist/preact.mjs'
import fetch from '../../../node_modules/unfetch/dist/unfetch.mjs'
import Form, { FormGroup, Input, TextArea } from '../../../node_modules/@depack/form/build/index.js'
import { Col } from './../../frontend/components/Bootstrap.jsx'
import ArticleEditor from './../ArticleEditor.jsx'

export default class AddCategory extends Component {
  constructor() {
    super()
    this.state = {
      loading: false,
      data: {},
      hint: '-',
      article: '',
    }
  }
  async componentDidMount() {
    const editing = !!this.props.id
    if (!editing) return
    this.setState({ editing: 1, loading: true })
    try {
      const res = await fetch(`/admin-data?categories&id=${this.props.id}`)
      const { error, data } = await res.json()
      if (error) this.setState({ error })
      else {
        const [d] = data
        this.setState({ data: d, hint: d.seo, article: d.article })
      }
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ loading: false })
    }
  }
  async submit(e) {
    this.setState({ error: null })
    e.preventDefault()
    const data = new FormData(this.form)
    data.append('article', this.state.article)
    this.setState({ formLoading: true })
    try {
      const res = await fetch('/admin-data?categories', {
        method: 'POST',
        body: data,
      })
      const { error } = await res.json()
      if (error) this.setState({ error })
      else this.setState({ success: 1 })
    } catch (error) {
      this.setState({ error })
    } finally {
      this.setState({ formLoading: false })
    }
    return false
  }
  getHint() {
    return `   ,    , , knedv.ru/<strong>${this.state.hint}</strong>/3--.`
  }
  onFormChange(values) {
    const newSeo = values.seo
  }
  render() {
    const hint = this.getHint()
    const { editing, resetImage } = this.state
    return                                                           h(Col,{},
      h('h1',{},this.state.editing ? '' : '',` `),
      editing && this.state.loading && h('span',{'className':"echo-loader"},`Loading`),
      !(editing && this.state.loading) &&
        h(Form,{onChange:(values) => {
          console.log(values)
          console.log(values.seo)
        },formRef:r => {
          this.form = r
        },onSubmit:this.submit.bind(this)},
          h(FormGroup,{label:"",help:"   ."},
            h(Input,{value:this.state.data.title,name:"title",placeholder:" ",required:true}),
          ),
          h(FormGroup,{help:hint,label:" "},
            h(Input,{required:true,value:this.state.data.seo,name:"seo",placeholder:"-"}),
          ),
          h(FormGroup,{label:"",help:"    ."},
            h(TextArea,{required:true,rows:"3",name:"description",placeholder:"     --     ,        ."},
              this.state.data.description,
            ),
          ),
          editing && !resetImage && h(FormGroup,{label:""},
            h('br'),
            h('img',{'src':this.state.data.cdnImage,'className':"img-fluid"}),
            h('a',{'onClick':(e) => {
              e.preventDefault()
              this.setState({ resetImage: true })
              return false
            },'href':"#",'className':"btn btn-outline-warning"},``),
          )
          ,
          (!editing || resetImage) && h(FormGroup,{label:"",help:",    ."},
            h(Input,{required:true,name:"image",type:"file",file:"1"}),
          ),
          h(ArticleEditor,{article:this.state.article,onSave:(html) => {
            this.setState({ article: html })
          }}),
          editing && h('input',{'value':this.props.id,'type':"hidden",'name':"id"}),

          h('button',{'disabled':this.state.formLoading,'type':"submit",'className':"btn btn-primary"},this.state.formLoading ? '...' : `${editing ? '' : ''}`),
          this.state.error && h('div',{'className':"alert alert-danger mt-3",'role':"alert"},this.state.error),
          this.state.success && h('div',{'className':"alert alert-success mt-3",'role':"alert"},`  `,editing ? '' : '',`!`),
        )
      ,
    )
  }
}

